.pc-headder {
  width: 100%;
  position: fixed;
  top: 20px;
  z-index: 99;
}

.pc-headder .inner {
  width: 100%;
  padding: 24px 64px;
  position: relative;
}

.pc-headder .inner .left {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.pc-headder .inner .left p {
  font-size: 20px;
  font-weight: 700;
  color: #ffffff;
  cursor: pointer;
}

.pc-headder .inner .left p.scrolled {
  display: none;
}

.pc-headder .inner .center {
  margin: 0 auto;
  width: 270px;
  max-width: 270px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 25px;
  transition: all 0.25s ease;
  backdrop-filter: blur(5px);
}

.pc-headder .inner .center > p {
  font-weight: 700;
  display: none;
  cursor: pointer;
}

.pc-headder .inner .center.scrolled > p {
  display: block;
}

.pc-headder .inner .center.scrolled {
  width: 582px;
  max-width: 582px;
  padding: 0 20px;
  border-color: rgba(255, 255, 255, 0.05);
  background-color: rgba(255, 255, 255, 0.8);
  box-shadow:
    rgba(0, 0, 0, 0.082) 0px 0.60219px 1.32481px -1.16667px,
    rgba(0, 0, 0, 0.075) 0px 2.28853px 5.03477px -2.33333px,
    rgba(0, 0, 0, 0.035) 0px 10px 22px -3.5px;
}

.pc-headder .inner .center .menu {
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 2px;
  padding: 4px 12px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 24px;
  box-shadow:
    rgba(0, 0, 0, 0.082) 0px 0px 0px 0px,
    rgba(0, 0, 0, 0.075) 0px 0px 0px 0px,
    rgba(0, 0, 0, 0.035) 0px 0px 0px 0px;
  background-color: rgba(255, 255, 255, 0.2);
  color: #ffffff;
  font-weight: 700;
  transition: all 0.2s
}

.pc-headder .inner .center .menu.scrolled {
  background-color: transparent;
  border: transparent;
  color: #344054;
}

.pc-headder .inner .center .menu .item {
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.pc-headder .inner .center .menu .item p {
  font-size: 14px;
  margin: 10px;
  transition: all 0.2s linear;
}

.pc-headder .inner .center .menu .item .p-hover {
  position: absolute;
  top: 0;
  transform: translateY(145%);
}

.pc-headder .inner .center .menu .item:hover .p-hover {
  transform: translateY(0%);
}

.pc-headder .inner .center .menu .item:hover .p {
  transform: translateY(-145%);
}

.mobile-headder {
  display: none;
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 99;
}

.mobile-headder .top-box {
  height: 68px;
  padding: 0 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: hsla(0, 0%, 100%, 0.9);
  box-shadow: 0 1px 1px 0 rgba(81, 99, 120, 0.2);
  transition: all 0.5s;
}

.mobile-headder .top-box.open {
  background-color: #ffffff;
  box-shadow: transparent;
}

.mobile-headder .top-box > p {
  font-size: 20px;
  font-weight: 700;
}

.mobile-headder .top-box .btn {
  width: 34px;
  height: 34px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  box-shadow:
    0 4px 30px 0 rgba(0, 0, 0, 0.1),
    0 0 0 1px rgba(0, 0, 0, 0.05);
  border-radius: 8px;
}

.mobile-headder .menu {
  height: 0;
  background-color: #ffffff;
  overflow: hidden;
  transition: all 0.3s ease;
  color: #242428;
  box-shadow: 0 2px 8px 0 #2f2b3d24;
}

.mobile-headder .menu.open {
  height: 100vh;
}

.mobile-headder .menu ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 20px;
  font-weight: 700;
}

.mobile-headder .menu ul li {
  margin: 20px 0;
}

@media (max-width: 810px) {
  .pc-headder {
    display: none;
  }

  .mobile-headder {
    display: block;
  }
}
