@font-face {
  font-family: 'NotoSans';
  src: url('../fonts/NotoSansKR-VariableFont_wght.ttf') format('truetype');
}

* {
  margin: 0;
  padding: 0;
  list-style: none;
  box-sizing: border-box;
  font-family: 'NotoSans';
}

.gradient-canvas {
  --gradient-color-1: #c3e4ff;
  --gradient-color-2: #6ec3f4;
  --gradient-color-3: #eae2ff;
  --gradient-color-4: #b9beff;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
}

.wrap {
  width: 100%;
  height: 100%;
  /* background-color: rgb(174, 174, 202); */
}

section {
  word-break: keep-all;
  overflow-wrap: break-word;
  position: relative;
  z-index: 10;
}

.section-btn {
  height: 64px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 24px;
  padding: 10px 28px;
  border: 2px solid #000000;
  border-radius: 18px;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  background: linear-gradient(to right, #ffffff 50%, #000000 50%);
  background-size: 210% 100%;
  background-position: right bottom;
  transition: all 0.3s ease-in;
}

.section-btn:hover {
  background-position: left bottom;
}

.section-btn p {
  color: #ffffff;
  font-weight: 700;
  font-size: 20px;
  transition: all 0.25s linear;
}

.section-btn .p-hover {
  position: absolute;
  top: 50%;
  transform: translateY(160%);
}

.section-btn:hover .p-hover {
  transform: translateY(-50%);
}

.section-btn:hover .p {
  transform: translateY(-160%);
}

.section-btn:hover p {
  color: #000000;
}

/* ----- section-1 ----- */
#section-1 {
  padding: 240px 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(5px);
}

#section-1 .text-box {
  text-align: center;
}

#section-1 .text-box p {
  font-size: 64px;
  letter-spacing: -1.5px;
  font-weight: 700;
}

#section-1 .text-box p:last-child {
  background: linear-gradient(
      90deg,
      rgb(114, 131, 231) 0%,
      rgb(71, 62, 162) 49%,
      rgb(79, 55, 145) 83.5%,
      rgb(67, 58, 160) 100%
    )
    text;
  -webkit-text-fill-color: transparent;
}

/* ----- section-2 ----- */
#section-2 {
  background-color: #ffffff;
  backdrop-filter: blur(5px);
}

#section-2 .inner {
  padding: 80px 140px 60px 140px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* background: linear-gradient(rgb(255, 244, 216) 0%, rgba(255, 251, 216, 0) 20%); */
}

#section-2 .inner .top-text-box {
  text-align: center;
  font-size: 58px;
  font-weight: 700;
  margin-bottom: 40px;
}

#section-2 .inner .top-text-box p:first-child {
  color: #00000066;
}

#section-2 .inner .list {
  width: 100%;
  display: flex;
  flex-flow: column;
  align-items: center;
}

#section-2 .inner .list .item {
  padding: 40px 0px;
  display: flex;
  align-items: center;
  gap: 60px;
}

#section-2 .inner .list .item .img-box {
  width: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  border-radius: 20px;
  padding-top: 50px;
}

#section-2 .inner .list .item:nth-child(1) .img-box {
  background-color: #ffdfb8;
}

#section-2 .inner .list .item:nth-child(2) .img-box {
  background-color: #d7adff;
}

#section-2 .inner .list .item:nth-child(3) .img-box {
  background-color: #6fe6e5;
}

#section-2 .inner .list .item:nth-child(4) .img-box {
  background-color: #eeb2fd;
}

#section-2 .inner .list .item .img-box img {
  width: 75%;
  object-fit: contain;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

#section-2 .inner .list .item .text-box {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: flex-start;
  gap: 35px;
}

#section-2 .inner .list .item .text-box > img {
  width: 44px;
  height: 44px;
}

#section-2 .inner .list .item .text-box .sub-text-box {
  width: 100%;
  display: flex;
  flex-flow: column;
  gap: 10px;
  font-weight: 700;
}

#section-2 .inner .list .item .text-box .sub-text-box p:first-child {
  color: #000000d9;
  font-size: 52px;
  letter-spacing: -1.5px;
}

#section-2 .inner .list .item .text-box .sub-text-box p:last-child {
  color: #00000066;
  font-size: 24px;
  letter-spacing: -0.5px;
}

/* ----- section-3 ----- */
#section-3 {
  /* background-color: #ffffff; */
  overflow: hidden;
}

#section-3 .inner {
  padding: 80px 140px 60px 140px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* background-color: rgb(230, 221, 255); */
}

#section-3 .inner .top-text-box {
  text-align: center;
  font-size: 58px;
  font-weight: 700;
  margin-bottom: 40px;
}

#section-3 .inner .top-text-box p:first-child {
  color: #00000066;
}

#section-3 .inner .list {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  max-width: 1440px;
}

#section-3 .inner .list .item {
  padding: 20px 0px;
  display: flex;
  flex-direction: column;
  flex: 1 1 0%;
  gap: 20px;
  will-change: transform;
  transition: all 0.25s;
}

#section-3 .inner .list .item .img-box {
  width: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  border-radius: 20px;
  padding-top: 50px;
}

#section-3 .inner .list .item:nth-child(1) .img-box {
  background-color: #ffdfb8;
}

#section-3 .inner .list .item:nth-child(2) .img-box {
  background-color: #d7adff;
}

#section-3 .inner .list .item:nth-child(3) .img-box {
  background-color: #6fe6e5;
}

#section-3 .inner .list .item:nth-child(4) .img-box {
  background-color: #eeb2fd;
}

#section-3 .inner .list .item .img-box img {
  width: 75%;
  object-fit: contain;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

#section-3 .inner .list .item .text-box {
  width: 100%;
  padding-left: 4px;
  padding-right: 40px;
  display: flex;
  flex-flow: column;
  gap: 10px;
  font-weight: 700;
}

#section-3 .inner .list .item .text-box p:first-child {
  font-size: 24px;
  letter-spacing: -0.5px;
}

#section-3 .inner .list .item .text-box p:last-child {
  color: #00000066;
  font-size: 18px;
  letter-spacing: -0.4px;
}

/* ----- section-4 ----- */
#section-4 {
  background-color: #ffffff;
}

#section-4 .inner {
  padding: 80px 140px 40px 140px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#section-4 .inner .top-text-box {
  text-align: center;
  font-weight: 700;
}

#section-4 .inner .top-text-box p {
  margin-bottom: 30px;
}

#section-4 .inner .top-text-box p:first-child {
  display: inline-block;
  padding: 10px 18px;
  border-radius: 24px;
  background-color: rgb(255, 230, 144);
  font-size: 18px;
  letter-spacing: -0.4px;
}

#section-4 .inner .top-text-box p:last-child {
  /* color: rgb(255, 253, 202); */
  font-size: 58px;
  letter-spacing: -2px;
}

#section-4 .inner .list {
  width: 80%;
  display: flex;
  flex-flow: column;
  align-items: center;
}

#section-4 .inner .list .item {
  margin: 40px 0 60px 0;
  padding: 40px 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 35px;
  border-radius: 20px;
  position: sticky;
  top: 0;
  will-change: transform;
  transition: all 0.25s;
}

#section-4 .inner .list .item:nth-child(1) {
  background-color: #d7adff;
  z-index: 1;
}

#section-4 .inner .list .item:nth-child(2) {
  background-color: #d7adff;
  top: 40px;
  z-index: 2;
}

#section-4 .inner .list .item:nth-child(3) {
  background-color: #6fe6e5;
  top: 80px;
  z-index: 3;
}

#section-4 .inner .list .item:nth-child(4) {
  background-color: #6fe6e5;
  top: 120px;
  z-index: 4;
}

#section-4 .inner .list .item img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 20px;
}

#section-4 .inner .list .item .text-box {
  width: 100%;
  font-weight: 700;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: 0px 10px;
  gap: 50px;
  font-weight: 700;
}

#section-4 .inner .list .item .text-box > p {
  color: #ffffff;
  font-size: 30px;
  letter-spacing: -1px;
}

#section-4 .inner .list .item .text-box .right {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 20px;
}

#section-4 .inner .list .item .text-box .right p {
  /* color: #efeff0cc; */
  font-size: 20px;
  letter-spacing: -0.5px;
}

/* ----- section-5 ----- */
#section-5 {
  background-color: #ffffff;
}

#section-5 .inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#section-5 .inner .box {
  width: 100%;
  max-width: 100%;
  padding: 120px 64px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(95.6491deg, rgb(0, 70, 213) 0%, rgb(0, 78, 235) 100%);
  box-shadow:
    rgba(0, 15, 46, 0.075) 0px 0px 0px 0px,
    rgba(0, 15, 46, 0.08) 0px 0px 0px 0px,
    rgba(0, 15, 46, 0.11) 0px 0px 0px 0px;
  position: relative;
  transition: all 0.5s ease;
}

#section-5 .inner .box.on {
  width: 95%;
  max-width: 1680px;
  border-radius: 16px;
}

#section-5 .inner .box.on .text-box {
  opacity: 1;
}

#section-5 .inner .box .text-box {
  opacity: 0;
  display: inline-block;
  text-align: center;
  transition: all 0.5s ease;
}

#section-5 .inner .box .text-box > p {
  color: #ffffff;
  font-size: 40px;
  font-weight: 700;
  margin-bottom: 40px;
}

#section-5 .inner .footer {
  width: 100%;
  max-width: 1680px;
  padding: 64px;
}

#section-5 .inner .footer .text-box {
  font-size: 16px;
  line-height: 35px;
}

#section-5 .inner .footer .text-box p:first-child {
  color: rgb(101, 103, 106);
}

#section-5 .inner .footer .text-box p:last-child {
  color: rgb(167, 167, 170);
}

.chat-wrap {
  position: fixed;
  bottom: 30px;
  right: 30px;
  z-index: 99;
}

.chat-wrap .inner {
}

.chat-wrap .inner img {
  width: 50px;
  height: 50px;
  cursor: pointer;
  position: relative;
  z-index: 99;
}

.chat-wrap .inner .message-box {
  width: 400px;
  height: 650px;
  padding: 20px;
  background-color: #f7f7f8;
  box-shadow: 0 2px 6px 5px #2f2b3d33;
  border-radius: 25px;
  transform: translateY(50px);
  transition: all 0.3s ease;
  position: fixed;
  bottom: 100px;
  right: 30px;
  z-index: -1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;
  opacity: 0;
  visibility: hidden;
}

.chat-wrap .inner .message-box.open {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
  z-index: 99;
}

.chat-wrap .inner .message-box .top {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.chat-wrap .inner .message-box .top p {
  font-size: 20px;
  font-weight: 700;
}

.chat-wrap .inner .message-box .top .btn {
  width: 34px;
  height: 34px;
  display: none;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  box-shadow:
    0 4px 30px 0 rgba(0, 0, 0, 0.1),
    0 0 0 1px rgba(0, 0, 0, 0.05);
  border-radius: 8px;
}

.chat-wrap .inner .message-box .body {
  height: 100%;
  overflow-y: auto;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 15px;
}

.chat-wrap .inner .message-box .body::-webkit-scrollbar {
  display: none;
}

.chat-wrap .inner .message-box .bottom {
  display: flex;
  height: 45px;
  align-items: center;
  justify-content: space-between;
  gap: 0 20px;
}

.chat-wrap .inner .message-box .bottom input {
  width: 75%;
  height: 100%;
  padding: 0 10px;
  border-radius: 12px;
  border: 1px solid #ccc;
  font-size: 14px;
}

.chat-wrap .inner .message-box .bottom input:focus {
  outline: none;
}

.chat-wrap .inner .message-box .bottom .btn {
  width: 20%;
  height: 100%;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  cursor: pointer;
  font-size: 16px;
}

/* .chat-wrap .inner .message-box .bottom svg {
  width: 90%;
  height: 90%;
} */

.chat-wrap .inner .message-box .bottom .btn p {
  width: 100%;
  height: 100%;
  line-height: 45px;
  color: #ffffff;
  background-color: #000000;
  border-radius: 10px;
  cursor: pointer;
}

/* 애니메이션 효과 */
.opacity-hidden {
  opacity: 0;
}

.bottom-to-top-off {
  opacity: 0;
  transform: translateY(150px);
}

.bottom-to-top {
  opacity: 1;
  transform: translateY(0);
  transition: all 1.5s ease;
}

.inquiry {
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  background-color: #f0ebf8;
}

.inquiry iframe {
  width: 100%;
  height: 100%;
  border: none;
  margin: 0;
}

/* ----- media-query ----- */
@media (max-width: 990px) {
  #section-2 .inner,
  #section-3 .inner,
  #section-4 .inner {
    padding: 50px 80px 20px 80px;
  }

  #section-4 .inner .list .item .text-box > p {
    font-size: 28px;
  }

  #section-4 .inner .list .item .text-box .right p {
    font-size: 18px;
  }

  #section-4 .inner .list {
    width: 100%;
  }
}

@media (max-width: 810px) {
  .section-btn {
    height: 52px;
  }

  .section-btn p {
    font-size: 16px;
  }

  #section-1 {
    padding: 160px 16px;
  }

  #section-1 .text-box p {
    font-size: 34px;
  }

  #section-2 .inner,
  #section-3 .inner,
  #section-4 .inner {
    padding: 40px 16px 20px 16px;
  }

  #section-2 .inner .top-text-box {
    font-size: 32px;
    margin-bottom: 20px;
  }

  #section-2 .inner .list .item {
    padding: 30px 0px;
    align-items: flex-start;
    flex-flow: column;
    gap: 15px;
  }

  #section-2 .inner .list .item .text-box {
    order: 1;
  }

  #section-2 .inner .list .item .text-box > img {
    display: none;
  }

  #section-2 .inner .list .item .text-box .sub-text-box {
    text-align: center;
  }

  #section-2 .inner .list .item .text-box .sub-text-box p:first-child {
    font-size: 32px;
  }

  #section-2 .inner .list .item .text-box .sub-text-box p:last-child {
    font-size: 18px;
  }

  #section-3 .inner .list {
    grid-template-columns: 1fr;
  }

  #section-3 .inner .top-text-box {
    font-size: 32px;
  }

  #section-3 .inner .list .item .text-box p:first-child {
    font-size: 18px;
  }

  #section-3 .inner .list .item .text-box p:last-child {
    font-size: 16px;
  }

  #section-4 .inner .top-text-box p:last-child {
    font-size: 32px;
  }

  #section-4 .inner .list .item {
    margin: 20px 0 20px 0;
    padding: 20px 20px;
    gap: 20px;
  }

  #section-4 .inner .list .item .text-box {
    flex-direction: column;
    gap: 20px;
  }

  #section-4 .inner .list .item .text-box > p {
    font-size: 24px;
  }

  #section-4 .inner .list .item .text-box .right p {
    font-size: 16px;
  }

  #section-5 .inner .box {
    padding: 100px 20px;
  }

  #section-5 .inner .box .text-box > p {
    font-size: 28px;
  }

  #section-5 .inner .footer {
    padding: 40px;
    text-align: center;
  }

  #section-5 .inner .footer .text-box {
    font-size: 15px;
  }

  .chat-wrap .inner .message-box {
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    border-radius: 0;
  }

  .chat-wrap .inner .message-box .top .btn {
    display: flex;
  }

  .chat-wrap .inner .message-box .bottom input {
    font-size: 12px;
  }

  .chat-wrap .inner .message-box .bottom .btn {
    font-size: 14px;
  }
}
